import React, { useEffect, useState } from "react";
import { useToasts } from "react-toast-notifications";
import { PixelCode } from "../../components/PixelScript";
import { PlaceOrder } from "../../firebase";
import { store } from "../../store";
import { CheckoutItem } from "./ChekoutItem";

export const Checkout = () => {
  const [basketCount, setBasketCount] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [basketString, setBasketString] = useState("");
  const basketSelector = store.getState().basket;
  const { addToast } = useToasts();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  store.subscribe(() => setBasketCount(store.getState().basket));
  useEffect(() => {
    setBasketCount(basketSelector);
  }, []);
  useEffect(() => {
    let total = totalPrice;
    let basketSTR = basketString || "";
    basketCount.forEach(
      (item) => (total = total + parseFloat(item.salePrice) * item.count)
    );
    basketCount.forEach(
      (item) =>
        (basketSTR =
          basketSTR + " " + item.titleru + " кол-во :" + item.count + ";")
    );

    setTotalPrice(total);
    setBasketString(basketSTR);
  }, [basketCount]);

  const onSubmit = async (e) => {
    e.preventDefault();

    const form = {};
    const formData = new FormData(e.target);
    // @ts-ignore
    for (const [key, value] of formData.entries()) {
      if (value) {
        form[key] = value;
      }
    }
    console.log(form);
    if (!form?.name || form?.name.length < 1) {
      addToast(
        currentLang === "ru" ? "Заполните поле имя!" : "Заповніть поле ім'я!",
        {
          appearance: "error",
          autoDismiss: true,
        }
      );
      return;
    } else if (!form?.phone || form?.phone.length < 10) {
      addToast(
        currentLang === "ru"
          ? "Номер телефона должен состоять из 10 цифр!"
          : "Номер телефону повинен складатися з 10 цифр!",
        {
          appearance: "error",
          autoDismiss: true,
        }
      );
      return;
    }
    form.basket = basketString + ` Общая цена заказа: ${totalPrice}`;
    await PlaceOrder(form);
    addToast(
      currentLang === "ru"
        ? "Ваш заказ принят! !"
        : "Ваше замовлення прийняте !",
      {
        appearance: "success",
        autoDismiss: true,
      }
    );
  };
  const currentLang = store.getState().lang;
  return (
    <>
      <PixelCode />
      <div className="section-padding pb-0">
        <div className="container">
          <form onSubmit={onSubmit}>
            <div className="row">
              <div className="col-xl-7">
                <h4>
                  {" "}
                  {currentLang === "ru" ? "Детали заказа" : "Деталi замовлення"}
                </h4>
                <div className="row">
                  <div className="form-group col-xl-12">
                    <label className="form-label">
                      {currentLang === "ru" ? "Полное имя" : "Повне ім'я"}
                      <span className="text-danger">*</span>
                    </label>
                    <input
                      type="text"
                      placeholder={
                        currentLang === "ru" ? "Полное имя" : "Повне ім'я"
                      }
                      name="name"
                      className="form-control"
                      required=""
                    />
                  </div>

                  <div className="form-group col-xl-12">
                    <label className="form-label">
                      {currentLang === "ru" ? "Город" : "Місто"}
                    </label>
                    <input
                      type="text"
                      placeholder={currentLang === "ru" ? "Город" : "Місто"}
                      name="town"
                      className="form-control"
                      required=""
                    />
                  </div>
                  <div className="form-group col-xl-6">
                    <label className="form-label">
                      {currentLang === "ru"
                        ? " Номер телефона"
                        : "Номер телефону"}
                      <span className="text-danger">*</span>
                    </label>
                    <input
                      type="text"
                      placeholder={
                        currentLang === "ru"
                          ? " Номер телефона"
                          : "Номер телефону"
                      }
                      name="phone"
                      className="form-control"
                      required=""
                    />
                  </div>
                  <div className="form-group col-xl-6">
                    <label className="form-label">
                      {currentLang === "ru"
                        ? " Удобный способ оплаты"
                        : "Зручний спосіб оплати"}
                    </label>
                    <input
                      type="text"
                      placeholder={
                        currentLang === "ru"
                          ? " Удобный способ оплаты"
                          : "Зручний спосіб оплати"
                      }
                      name="sposobOplaty"
                      className="form-control"
                      required=""
                    />
                  </div>

                  <div className="form-group col-xl-12">
                    <label className="form-label">Служба доставки</label>
                    <select className="form-control">
                      <option value="">Служба доставки</option>
                      <option value="novaPoshta">Новая почта</option>
                      <option value="intaym">Интайм</option>
                      <option value="mist">МистЭкспресс</option>
                      <option value="mejdunarodnayaDostavka">
                        Международная доставка
                      </option>
                    </select>
                  </div>
                  <div className="form-group col-xl-6">
                    <label className="form-label">
                      {currentLang === "ru"
                        ? "Номер отделения службы доставки"
                        : "Номер відділення служби доставки"}
                    </label>
                    <input
                      type="text"
                      placeholder={
                        currentLang === "ru"
                          ? "Номер отделения службы доставки"
                          : "Номер відділення служби доставки"
                      }
                      name="novaPoshta"
                      className="form-control"
                      required=""
                    />
                  </div>
                  <div className="form-group col-xl-12 mb-0">
                    <label className="form-label">
                      {currentLang === "ru" ? "Комментарий" : "Коментарiй"}
                    </label>
                    <textarea
                      name="comment"
                      rows="5"
                      className="form-control"
                      placeholder={
                        currentLang === "ru" ? "Комментарий" : "Коментарiй"
                      }
                    ></textarea>
                  </div>
                </div>
              </div>
              <div className="col-xl-5 checkout-billing mt-4 mt-xl-0">
                <div className="table-responsive-md">
                  <table className="andro_responsive-table">
                    <thead>
                      <tr>
                        <th> Товар</th>
                        <th>{currentLang === "ru" ? "Кол-во" : "Кiль-сть"}</th>
                        <th>{currentLang === "ru" ? "Цена" : "Цiна"}</th>
                      </tr>
                    </thead>
                    <tbody>
                      {basketCount.map((item, index) => {
                        return <CheckoutItem key={index} data={item} />;
                      })}

                      <tr className="total">
                        <td>
                          <h6 className="mb-0">
                            {currentLang === "ru"
                              ? "Общая цена"
                              : "Загальна цiна"}
                          </h6>
                        </td>
                        <td></td>
                        <td>
                          <strong>{totalPrice} ГРН</strong>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                {basketCount.length > 0 && (
                  <button
                    onClick={() => {
                      window.localStorage.clear();
                      window.location.reload();
                    }}
                    type="submit"
                    className="btn btn-block"
                  >
                    {currentLang === "ru"
                      ? "Очистить корзину"
                      : "Очистити кошик"}
                  </button>
                )}
                <button type="submit" className="btn btn-block">
                  {currentLang === "ru"
                    ? "Подтвердить заказ"
                    : "Пiдтвердити замовлення"}
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};
